<template>
    <v-app toolbar footer>
        <v-toolbar class="red" dark fixed>
            <!-- <v-toolbar-side-icon @click.stop="drawer = !drawer"></v-toolbar-side-icon> -->
            <!-- <v-toolbar-title>ListShare</v-toolbar-title> -->
            <v-text-field
                solo
                label="Create a new list…"
                single-line
                prepend-icon="note_add"
                v-model="newListName"
                @keyup.native.enter="newList"
            ></v-text-field>
        </v-toolbar>
        <main>
            <v-container fluid>
                <router-view></router-view>
            </v-container>
        </main>
        <v-footer class="indigo">
        </v-footer>
    </v-app>
</template>

<script>
 export default {
     name: 'app',
     data: function() {
         return {
             newListName: ''
         }
     },

     methods: {
         newList(evt) {
             this.$router.push({
                 path: `/list/${this.newListName}`,
             })
             this.newListName = ''
         },
     },
 }
</script>

<style>
</style>



