<template>
    <div>
        <h2 class="display-2" v-if="notFound">
            '{{ name }}' doesn't exist. Create it now!
        </h2>
        <v-list>
            <template v-for="(item, idx) in list">
                <v-list-tile
                    :key="idx"
                >
                    <v-list-tile-action>
                        <v-checkbox
                            v-model="status[idx]"></v-checkbox>
                    </v-list-tile-action>
                    <v-list-tile-content>
                        <v-text-field
                            single-line
                            label="Text here…"
                            v-model="list[idx]"
                            :disabled="status[idx]"
                            :class="{ strikethrough: status[idx] }"
                        ></v-text-field>
                    </v-list-tile-content>
                </v-list-tile>
            </template>
            <v-btn outline
                   large
                   fab
                   @click="addRow"
                   class="red--text">
                <v-icon x-large>add_circle</v-icon>
            </v-btn>
        </v-list>
        <v-progress-linear
            v-bind:indeterminate="true"
            v-if="savingInProgress"
        ></v-progress-linear>
    </div>
</template>

<style scoped>
 .strikethrough input {
     text-decoration: line-through;
 }
</style>

<script>
 import {
     writeList,
     getList,
     checkAvailability
 } from '@/api.js'

 import { debounce } from 'lodash'

 //const isCompleted = (text) => /<s>.*<\/s>/gi.test(text)
 const DONE = "DONE "
 const isCompleted = text => text.slice(0, DONE.length) === DONE
 const negate = function(f) {
     return function() {
         return !f(...arguments)
     }
 }
 /* const wrapStrikethrough = (text) => `<s>${text}</s>`
  * const deStrikethrough = (text) => /<s>(.*)<\/s>/.exec(text)[1]*/
 const makeDone = text => `${DONE}${text}`
 const makeUnDone = text => text.slice(DONE.length)

 export default {
     props: ['name'],
     
     data: function() {
         return {
             list: [],
             status: [],
             notFound: false,
             savingInProgress: false,
         }
     },

     created: async function() {
         await this.hydrate()
     },

     methods: {
         async hydrate() {
             let avail = await checkAvailability(this.name)
             if (avail === false) {
                 this.notFound = false
                 let list = await getList(this.name)
                 if (list.exists) {
                     this.list = list.lines
                     this.status = this.list.map(isCompleted)
                 }
             } else {
                 this.notFound = true
                 this.list = []
             }
         },
         
         checked(idx) {
             return isCompleted(this.list[idx])
         },

         addRow() {
             if (this.list.length === 0 || this.list[this.list.length-1].length > 0) {
                 this.list.push('')
             }
         },

         async saveOnServer() {
             this.savingInProgress = true
             let r = await writeList(this.name, this.list)
             this.savingInProgress = false
             if (r.status != 200) {
                 console.error(`Save on server failed on '${this.name}'`)
                 return false
             }
             return true
         },
     },

     watch: {
         '$route': async function() {
             this.status = []
             await this.hydrate()
         },
         
         status: function(newStatus) {
             for (let i = 0; i < newStatus.length && i < this.list.length; i++) {
                 let statusSays = newStatus[i]
                 let listSays = isCompleted(this.list[i])
                 if (listSays !== statusSays) {
                     this.list.splice(i, 1, statusSays ? makeDone(this.list[i]) : makeUnDone(this.list[i]))
                 }
             }
         },

         list: function() {
             if (this.list.length > 1) {
                 debounce(this.saveOnServer, 5000)()
             }
         },
     },
 }
</script>



