import Vue from 'vue'
import Router from 'vue-router'
import Nada from '@/components/Nada'
import List from '@/components/List'

Vue.use(Router)

export default new Router({
    routes: [
        {
            path: '/',
            name: 'Nada',
            component: Nada,
        },
        {
            path: '/list/:name',
            name: 'List',
            component: List,
            props: true,
        },
    ]
})



