//const HOST = `http://${window.location.hostname}:4500`
//const HOST = '/'
const HOST = 'http://108.83.213.95'

export async function checkAvailability(name) {
    let params = new URLSearchParams()
    params.append('name', name)
    try {
        let r = await fetch(`${HOST}/check?name=${encodeURIComponent(name)}`, {
            method: 'GET',
        })
        switch (r.status) {
        case 200:
            let j = await r.json()
            return j.availability
        case 500:
            console.error(`500 error on checking '${name}': ${r.data}`)
            return false
        default:
            console.error(`Something happened`)
            return false
        }
    } catch (e) {
        console.error(`Something happened`)
        return false
    }
}

export async function getList(name) {
    try {
        let r = await fetch(`${HOST}/view?name=${encodeURIComponent(name)}`, {
            method: 'GET',
        })
        switch(r.status) {
        case 200:
            return {
                exists: true,
                lines: await r.json(),
            }
        case 404:
            return {
                exists: false,
            }
        default:
            console.error(`Something happened; ${r.status} -- ${r.statusText}`)
            return {
                exists: false,
            }
        }
    } catch (e) {
        console.error(e)
        return {
            exists: false,
        }
    }
}

export async function writeList(name, list) {
    try {
        let r = await fetch(`${HOST}/write`, {
            method: 'POST',
            body: JSON.stringify({
                list,
                name,
            }),
        })
        if (r.status === 200) {
            return true
        } else {
            console.error(`Server returned ${r.status} -- '${r.statusText}' on writeList(${name}, ${list})`)
            return false
        }
    } catch (e) {
        console.log(`Something happened: ${e}`)
        return false
    }
}



